.countdown {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--701);
  font-family: "Poppins", sans-serif;
  scrollbar-width: thin;
  overflow: hidden;
}
.countdown::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: hsla(0deg, 0%, 4%, 0);
}
.countdown::-webkit-scrollbar-thumb {
  background-color: hsla(0deg, 0%, 0%, 0);
  border-radius: 100vw;
}
.countdown #time {
  display: flex;
  gap: 30px;
  transform: scale(75%);
}
.countdown #time .circle {
  position: relative;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.countdown #time .circle svg {
  position: relative;
  width: 150px;
  height: 150px;
  transform: rotate(270deg);
}
.countdown #time .circle svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 8;
  stroke: #282828;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}
.countdown #time .circle svg circle:nth-child(2) {
  stroke: var(--clr);
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
}
.countdown #time div {
  position: absolute;
  text-align: center;
  font-weight: 500;
  color: #fff;
  font-size: 1.5em;
}
.countdown #time div span {
  position: absolute;
  transform: translateX(-50%) translateY(-10px);
  font-size: 0.35em;
  font-weight: 300;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}
.countdown #time .dots {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 1000;
}
.countdown #time .dots:before {
  content: "";
  position: absolute;
  top: -3px;
  width: 15px;
  height: 15px;
  background: var(--clr);
  border-radius: 50%;
  box-shadow: 0 0 20px var(--clr), 0 0 60px var(--clr);
}
.countdown .newYear {
  font-size: 8em;
  font-weight: 500;
  color: #fff;
  text-align: center;
  line-height: 0.6em;
  display: none;
}
.countdown .newYear span {
  font-size: 0.5em;
  font-weight: 300;
}

.home__body {
  background-color: var(--701);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.home__body div {
  height: 150px;
  width: 150px;
}
.home__body div:nth-of-type(1) {
  background-color: var(--901);
}
.home__body div:nth-of-type(2) {
  background-color: var(--801);
}
.home__body div:nth-of-type(3) {
  background-color: var(--701);
}
.home__body div:nth-of-type(4) {
  background-color: var(--601);
}
.home__body div:nth-of-type(5) {
  background-color: var(--501);
}

:root {
  --900: #cdb4db;
  --800: #ffc8dd;
  --700: #ffafcc;
  --600: #bde0fe;
  --500: #a2d2ff;
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  --boxActive: hsla(283, 82%, 20%, 0.545);
  --boxBorder: hsla(0, 0%, 100%, 0.425);
  --boxHover: hsla(0, 0%, 100%, 0.06);
  --box-bg: hsl(0, 0%, 8%);
  --box-t: hsl(0, 0%, 60%);
}

.test__body {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  font-family: "Roboto", sans-serif;
  background-color: var(--701);
  min-height: 100vh;
  padding-top: 56px;
  padding-left: 72px;
}
.test__body nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 56px;
  background-color: red;
  position: fixed;
  top: 0;
  right: 0;
}
.test__body nav .menu-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 72px;
  background-color: green;
}
.test__body nav .menu-btn img {
  width: 24px;
  height: 24px;
}
.test__body header iframe {
  width: 100%;
  overflow: hidden;
}
.test__body .navbar {
  position: relative;
  height: 48px;
  border-bottom: solid 1px rgba(255, 255, 255, 0.2);
}
.test__body .navbar__inside_contanier {
  height: 100%;
  max-width: 600px;
  background-color: rgb(39, 12, 192);
  margin-inline: auto;
}
.test__body .navbar__inside_contanier__list {
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: space-around;
}
.test__body .navbar__inside_contanier__list li {
  display: flex;
  align-items: center;
  height: 100%;
}
.test__body .navbar__inside_contanier__list li a {
  color: red;
}
.test__body main {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.test__body main .yt-video-contanier {
  display: flex;
  flex-direction: column;
}
.test__body main .yt-video-contanier .video-thumbnail {
  border-radius: 10px;
}
.test__body main .yt-video-contanier .video-data {
  display: flex;
  height: 80px;
  padding-top: 10px;
}
.test__body main .yt-video-contanier .video-data .channel-logo {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.test__body main .yt-video-contanier .video-data .channel-logo img {
  border-radius: 50%;
}
.test__body main .yt-video-contanier .video-data .title-channel-name p:nth-of-type(1) {
  color: #fff;
}
.test__body main .yt-video-contanier .video-data .title-channel-name p:nth-of-type(2) {
  color: rgba(255, 255, 255, 0.3294117647);
}

aside {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
}

aside {
  /*
  aside ↓↓
        ==> [div] .home-svg
        ||    ↓↓
        ||    ==> [img]
        ||
        ==> [div] .days  ( DAY 1 to DAY 100 ) 
              ↓↓
              ==> [a]
                    ↓↓
                    ==> [span]
  */
  width: 78px;
  padding: 4px;
  height: calc(100% - 56px);
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--601);
  overflow: auto;
  scrollbar-width: none;
}
aside::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: hsla(0deg, 0%, 4%, 0);
}
aside::-webkit-scrollbar-thumb {
  background-color: hsla(0deg, 0%, 0%, 0);
  border-radius: 100vw;
}
aside .home-svg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--801);
  height: 64px;
  width: 64px;
}
aside .home-svg svg {
  color: red;
  width: 24px;
}
aside .days {
  background-color: red;
}
aside .days a {
  color: white;
}

.days-container {
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
  border-radius: 0;
}

.days-container:hover > .day::after {
  opacity: 1;
}

.day {
  cursor: pointer;
  height: 64px;
  width: 64px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.day:hover::before {
  opacity: 1;
}

.day::before,
.day::after {
  content: "";
  height: 100%;
  width: 100%;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  transition: opacity 500ms;
}

.day::before {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxHover), transparent 40%);
  z-index: 3;
}

.day::after {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxBorder), transparent 30%);
  z-index: 1;
}

.day > .day-number {
  background-color: var(--box-bg);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 2;
}
.day > .day-number span {
  color: var(--box-t);
  font-weight: 600;
  font-size: 1rem;
  margin: auto;
  text-align: center;
}

.day > .active {
  background-color: var(--boxActive);
}